.login-container {
  color: purple;
  display: flex;
  justify-content: center;
}

.form-container {
  align-items: center;
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.form-container img {
  height: 668px;
}

.formulario {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 34px;
  width: 400px;
}

.formulario h1 {
  font-size: 73px;
}

.formulario input {
  border: 1px solid purple;
  border-radius: 10px;
  height: 50px;
  margin-bottom: 40px;
  padding-left: 15px;
  width: 250px;
}

.formulario button {
  background-color: black;
  border-radius: 10px;
  color: white;
  font-size: 16px;
  height: 50px;
  width: 200px;
}

.formulario button:disabled {
  background-color: gray;
  color: darkgray;
}

.formulario button:hover {
  cursor: pointer;
}

.formulario button:active {
  background-color: white;
  border: 2px solid aqua;
  color: rgba(57, 6, 49, 255);
}
